import React, { PureComponent } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { actionCreators } from "./store";
import { DetailWrapper, Header, Content, Info } from "./style";
class Detail extends PureComponent {
  render() {
    return (
      <DetailWrapper>
        <Header>{this.props.title}</Header>
        <Info>
          <img
            src="https://upload.jianshu.io/users/upload_avatars/19716661/30b70975-5cc8-4a9a-acf6-87aad93b89c7.jpg"
            alt=""
          />
          <div>
            <p>作者姓名</p>
          </div>
        </Info>
        <Content dangerouslySetInnerHTML={{ __html: this.props.content }} />
      </DetailWrapper>
    );
  }
  componentDidMount() {
    this.props.getDetail(this.props.match.params);
  }
}
const mapstate = state => ({
  title: state.getIn(["detail", "title"]),
  content: state.getIn(["detail", "content"])
});
const mapdispath = dispath => ({
  getDetail(id) {
    dispath(actionCreators.getDetail(id));
  }
});
export default connect(mapstate, mapdispath)(withRouter(Detail));
