import styled from "styled-components";
export const DetailWrapper = styled.div`
  width: 620px;
  padding-bottom: 100px;
  margin: 0 auto;
  overflow: hidden;
`;
export const Header = styled.div`
  margin: 50px 0 20px 0;
  font-size: 34px;
  line-height: 44px;
  color: #333;
  font-weight: bold;
`;
export const Info = styled.div`
  overflow: hidden;
  margin-bottom:32px;
  img {
    float: left;
    width:48px;
    border-radius:24px;
  }
  div{
    float:left;
    line-height:48px;
    margin-left:20px;
  }
`;
export const Content = styled.div`
  color: #2f2f2f;
  img {
    width: 100%;
  }
  p {
    margin: 25px 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.8;
  }
`;
